:root {
  --primary-color1: #495e57;
  --primary-color2: #f4ce14;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  margin: 0 auto;
  padding: 0;
}
html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
.container {
  max-width: 1350px;
  min-width: 290px;
  margin: 0;
  padding: 0 60px;
}

form {
  width: 300px;
}
.btn {
  display: inline-block;
  text-decoration: none;
  background-color: #f4ce14;
  padding: 15px 20px;
  border-radius: 10px;
  transition: all 300ms ease-in-out;
}
.btn:hover,
.order-button:hover {
  color: #f4ce14;
  background-color: black;
}

.about {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: rgb(234, 226, 183, 0.3);
  padding: 200px 0;
  gap: 40px;
  opacity: 0.9;
}
.about .info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 50px;
  background-color: rgb(241, 241, 239, 0.3);
}

.about h1,
.menu h1 {
  font-size: 2rem;
  color: black;
}

.about p {
  /* width: 70%; */
  line-height: 1.4;
  letter-spacing: 0.1rem;
  font-weight: 600;
  color: #333;
  margin-top: 10px;
}

.about-images img {
  width: 80%;
  rotate: 5deg;
  margin-right: 30px;
}
.simg {
  transform: rotate(-10deg);
}

.about-images {
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  /* background-color: #fff; */
  /* box-shadow: 10px 2px 10px rgba(10, 12, 12, 0.5); */
  border-bottom-right-radius: 50%;
  border-top-left-radius: 50%;
}

