  list-style-type: none;
  margin: 20px 0;
}
.menu ul li {
  font-size: 1.2rem;
  line-height: 1.6;
  letter-spacing: 0.1rem;
} */

.form-submit-btn input {
  margin-top: 30px;
  padding: 10px 14px;
  background-color: #f4ce14;
  border-radius: 10px;
  border: none;
  outline: none;
  transition: all 400ms ease-in-out;
}

.form-submit-btn input:hover {
  color: white;
  background-color: black;
  box-shadow: 10px 5px 10px rgba(128, 88, 232, 0.1);
  /* box-shadow: 10px 2px 10px rgba(248, 143, 94, 0.1); */
}

.footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  gap: 100px;

  margin: auto;
}

.di-1, .di-2{
  display: flex;
  gap:30px

}
.di-2{
  justify-content: flex-end;
}

.logo {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
}
.logo p,
.contact p {
  color: #333;
}
.footer-section {
  display: flex;
  justify-content: center;
  margin: 100px auto;
}
.footer-nav-items {
  height: 100%;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
}

.footer-nav-items li a,
.s-btn {
  text-decoration: none;
  color: black;
  font-weight: 500;
}

.footer-nav-items li a {
  transition: all 400ms ease-in-out;
}

.footer-nav-items li a:hover {
  text-decoration: 2px solid black underline;
}

.s-btn {
  background-color: rgb(169, 169, 173);
  /* width: 60%; */
  padding: 10px 50px;
  text-align: center;
  border: none;
  border-radius: 5px;
  color: black;
  transition: all 400ms ease-in-out;
}

.s-btn:hover {
  color: rgb(169, 169, 173);
  background-color: black;
}

.contact-info {
  margin-top: 15px;
}
.footer.contact,
.footer.social-media {
  justify-content: end;
}

.social-media {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: fit-content;
}
.hl {
  background-color: rgba(73, 94, 87, 0.2);
  padding: 100px 0;
}
.highlights-heading {
  /* margin: 100px auto; */
  padding: 100px 0;
  display: flex;
  justify-content: space-between;
  /* padding: 0 140px; */
  margin: auto;
}

.online-menu-button {
  padding: 14px 20px;
  background-color: #f4ce14;
  border: none;
  font-weight: 600;
  font-size: 1.3rem;
  border-radius: 10px;
}

.highlights {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  gap: 30px;
  align-items: center;
}
.recepie {
  background-color: #fbdabb;
  border-radius: 10px;
  padding-bottom: 20px;
}
.recepie p {
  color: #333;
  margin: 10px 0;
}
.recepie-image {
  width: 100%;
  /* height:230px;   */
}

.description {
  margin: 10px;
}
.description h4,
.description h6 {
  margin-top: 10px;
}

.order-button {
  padding: 10px 20px;
  border: none;
  background-color: #f4ce14;
  margin-top: 10px;
  margin-left: 10px;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: 600;
}
